import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';

import {environment} from '../environments/environment';

import {Router} from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import {ModalDismissReasons, NgbModal} from "@ng-bootstrap/ng-bootstrap";

@Injectable()
export class AppHttpService {
  protected url: string;
  protected header: HttpHeaders;
  protected token = null;
  protected httpOptions: HttpHeaders;
  protected bodyRequest:any;
  modalReference: any;
  closeResult = '';

  request() {
    return this.http;
  }

  constructor(protected http: HttpClient, protected router: Router, private toast: ToastrService, private modalService: NgbModal) {
    this.setAccessToken();
  }

  setAccessToken() {
    this.token = localStorage.getItem('token');
    this.header = new HttpHeaders({'Authorization': 'Bearer ' + this.token, 'Accept': 'application/json'});
  }

  builder(resource: string) {
    this.url = environment.server_url + '/api/v1/admin/' + resource;
    return this;
  }

  list(options: any = {}): Promise<any> {
    let url = this.url;
    if (options.filters !== undefined) {
      url = url + '?';
      let filters = options.filters;
      let where = [];
      filters.forEach((item, index) => {
        let field = Object.keys(item)[0];
        let value = item[field];
        where.push({field: value});
        url = url + 'where[' + field + ']=' + value + '&';
      });
      let observable = this.http.get(url, {headers: this.header});
      return this.toPromise(observable);
    } else {
      let observable = this.http.get(url, {headers: this.header});
      return this.toPromise(observable);
    }
  }

  search(search: string): Promise<any> {
    let observable = this.http.get(this.url + '?value=' + search, {headers: this.header});
    return this.toPromise(observable);
  }

  view(id: number, url: string, include: string = ''): Promise<any> {
    let observable = this.http.get(this.url + url + '/' + id + include, {headers: this.header});
    return this.toPromise(observable);
  }

  update(id: number, data: object): Promise<any> {
    let observable = this.http.put(this.url  + '/' + id, data, {headers: this.header});
    return this.toPromise(observable);
  }

  insert(data: object): Promise<any> {
    let observable = this.http.post(this.url, data, {headers: this.header});
    return this.toPromise(observable);
  }

  delete(id: number): Promise<any> {
    let observable = this.http.delete(this.url + '/' + id, {headers: this.header});
    return this.toPromise(observable);
  }
  deleteImage(): Promise<any> {
    const observable = this.http.delete(this.url, {
      headers: this.header
    });
    return this.toPromise(observable);
  }

  // Open default modal
  open(content,config) {
    this.modalReference = this.modalService.open(content, config);
    this.modalReference.result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  // This function is used in open
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  protected toPromise(request) {
    //document.querySelector(".ng-star-inserted").style.position = "fixed";
    return request.toPromise()
      .then((res) => {
        return res || {}
      })
      .catch((err) => {
        let message = 'Algo deu errado, informe o erro ' + err.status + ' ao administrador';
        switch (err.status) {
          case 401:
            message = 'Você não tem permissão para acessar, informe um usuario e senha validos';
            localStorage.clear();
            if(this.modalReference != null) {
              this.modalReference.close();
            }
            this.toast.error(message, 'Erro');
            this.router.navigate(['/login']);
            break;
          case 403:
            message = err.error.error_description;
            if(this.modalReference != null) {
              this.modalReference.close();
            }
            this.toast.error(message, 'Erro');
            break;
          case 500:
            this.toast.error(message, 'Erro');
            this.router.navigate(['/pages/error']);
            break;
          case 422:
            this.bodyRequest = JSON.parse(err._body);
            this.toast.error(this.bodyRequest.message, this.bodyRequest.title);
            break;
          case 406:
            this.bodyRequest = err.error;
            this.toast.error(this.bodyRequest.message, this.bodyRequest.title);
            break;
          case 404:
            break;
        }
      });
  }
}
